@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

/* Slider */
.slick-loading .slick-list { background: #fff url("../img/ajax-loader.gif") center center no-repeat; }

/* Icons */
@font-face { font-family: "slick"; src: url("../fonts/slick.eot"); src: url("../fonts/slick.eot?#iefix") format("embedded-opentype"), url("../fonts/slick.woff") format("woff"), url("../fonts/slick.ttf") format("truetype"), url("../fonts/slick.svg#slick") format("svg"); font-weight: normal; font-style: normal; }

/* Arrows */
.slick-prev, .slick-next { position: absolute; display: block; height: 20px; width: 20px; line-height: 0px; font-size: 0px; cursor: pointer; background: transparent; color: transparent; top: 50%; margin-top: -10px; padding: 0; border: none; outline: none; }

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus { outline: none; background: transparent; color: transparent; }

.slick-prev:hover:before, .slick-prev:focus:before, .slick-next:hover:before, .slick-next:focus:before { opacity: 1; }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before { opacity: 0.25; }

.slick-prev:before, .slick-next:before { font-family: "slick"; font-size: 20px; line-height: 1; color: white; opacity: 0.75; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.slick-prev { left: -25px; }

[dir="rtl"] .slick-prev { left: auto; right: -25px; }

.slick-prev:before { content: "←"; }

[dir="rtl"] .slick-prev:before { content: "→"; }

.slick-next { right: -25px; }

[dir="rtl"] .slick-next { left: -25px; right: auto; }

.slick-next:before { content: "→"; }

[dir="rtl"] .slick-next:before { content: "←"; }

/* Dots */
.slick-dots { position: absolute; bottom: -60px; list-style: none; display: block; text-align: center; padding: 0; width: 100%; }

.slick-dots li { position: relative; display: inline-block; cursor: pointer; margin-left: 2px; }

.slick-dots li button { display: block; padding: 3px; background: none; outline: none; line-height: 0px; font-size: 0px; color: red; cursor: pointer; }

.slick-dots li button:before { content: ""; display: block; height: 10px; width: 10px; box-sizing: border-box; border-radius: 50%; background: #d4d3d4; transition: all 0.1s ease; }

.slick-dots li button:hover:before { background: #1ab798; }

.slick-dots li.slick-active button:before { background: #1ab798; }

body, html { font-family: 'Lato', sans-serif; position: relative; font-size: 14px; line-height: 1.4; }

img { max-width: 100%; }

.wrapper { max-width: 1430px; padding: 0 15px; margin: 0 auto; }

.title { font-size: 30px; margin-bottom: 35px; text-align: center; }

.h2 { position: relative; display: inline-block; font-family: 'Raleway', sans-serif; font-size: 28px; font-weight: bold; color: #1ab798; }

.h2:before { content: ''; position: absolute; width: 60px; height: 1px; background: #babdc1; left: -72px; top: 50%; transform: translateY(-50%); }

.h2:after { content: ''; position: absolute; width: 60px; height: 1px; background: #babdc1; right: -72px; top: 50%; transform: translateY(-50%); }

.s-line { font-size: 0; width: 170px; margin: 0 auto; margin-top: 8px; }

.s-line span { position: relative; display: inline-block; width: 78px; height: 2px; background: #343c3f; }

.s-line__left { margin-right: 2px; }

.s-line__left:after { content: ''; position: absolute; width: 11px; height: 2px; transform: rotate(122deg); background: #343c3f; right: -7px; top: -4px; }

.s-line__right { margin-left: 2px; }

.s-line__right:before { content: ''; position: absolute; width: 11px; height: 2px; transform: rotate(122deg); background: #343c3f; left: -7px; top: 4px; }

.btn { font-size: 16px; color: #fff; padding: 5px 15px; background-color: #c0af96; border: 2px solid #c0af96; }

.btn-form { max-width: 300px; width: 100%; }

.btn:hover { color: #c0af96; background-color: #fff; }

.btn:focus { box-shadow: none; }

.progress-container { position: absolute; bottom: 0; left: 0; width: 100%; height: 3px; margin-bottom: 0px; overflow: hidden; background-color: transparent; z-index: 1500; }

.progress-bar { width: 0%; float: left; height: 100%; z-index: 99; max-width: 100%; background-color: #fc3865; transition: width .6s ease; }

.m-0 { margin: 0; }

.header { min-height: 100vh; background-image: url(../img/header-section-bg.jpg); background-size: cover; background-position: center center; background-attachment: fixed; }

.header__nav { position: relative; background-color: #fff; box-shadow: 0 2px 20px 5px #f1f1f1; }

.header__nav .logo { position: absolute; display: none; left: 50px; height: 62px; }

.header__nav .logo img { height: 100%; }

.header__nav.fixed { position: fixed; top: 0; left: 0; right: 0; padding: 10px 0; z-index: 9999; }

.topline { position: relative; padding: 25px 0 10px 0; }

.topline__content { display: flex; justify-content: space-between; align-items: center; }

.main-nav { display: flex; align-items: center; justify-content: center; padding: 10px 0; }

.main-nav__item { margin: 0 20px; padding: 10px 0; }

.main-nav__link { position: relative; display: block; font-size: 16px; color: #000; text-decoration: none; }

.main-nav__link:before { content: ''; position: absolute; bottom: -4px; width: 0; left: 50%; height: 1px; background: #ab9256; transform: translateX(-50%); transition: all .3s; }

.main-nav__link:hover { color: #000; text-decoration: none; }

.main-nav__link:hover:before { width: 100%; }

.mobile-menu-btn { position: absolute; }

.about__content { padding: 80px 0; }

.about__descr { display: flex; flex-wrap: wrap; }

.about__block { flex: 1 0 32%; margin: 35px 0; }

.about-item { display: flex; }

.about-item__content { flex: 1 0 calc(100% - 30px); margin-left: 30px; }

.about-item__icon { flex: 1 0 30px; }

.about-item__title { font-size: 18px; font-weight: bold; text-transform: uppercase; }

.about-item__text { font-size: 14px; }

.servises { background: #f8f8f8; background-image: url(../img/bg-1.jpg); background-size: cover; background-position: center center; background-attachment: fixed; }

.servises__content { padding: 80px 35px; background-color: rgba(255, 255, 255, 0.75); }

.servises__descr { max-width: 1100px; margin: 0 auto; }

.servises-item { display: flex; margin-bottom: 50px; }

.servises-item__img { flex: 0 0 240px; margin: 0 40px 0 0; }

.servises-item__title { font-size: 20px; margin-bottom: 15px; font-weight: bold; }

.servises-item__text { font-size: 16px; }

.servises-item_right .servises-item__img { margin: 0 0 0 40px; }

.feedback { padding: 80px 0; }

.order-form { max-width: 800px; margin: 0 auto; }

.order-form__inner { display: flex; justify-content: space-between; margin-bottom: 35px; }

.order-form__field { position: relative; z-index: 1; display: inline-block; width: 350px; vertical-align: top; }

.order-form__field-big { width: 100%; }

.order-form__btn { text-align: center; }

.order-form__textarea { font-size: 14px; height: 200px; width: 100%; color: #5198fc; padding: 10px; border: 1px solid #5198fc; resize: none; }

.order-form__textarea::placeholder { color: #5198fc; }

.order-form__input { position: relative; display: block; float: right; padding: 0.8em; border: none; border-radius: 0; background: #f0f0f0; color: #aaa; width: 100%; background: transparent; color: #5198FC; font-weight: bold; font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; -webkit-appearance: none; /* for box shadows to show on iOS */ }

.order-form__input:focus + .order-form__label .graphic--madoka { stroke-dashoffset: 0; }

.order-form__input:focus + .order-form__label .order-form__label-content { transform: scale3d(0.81, 0.81, 1) translate3d(0, -40px, 0); }

.order-form__input:focus + .order-form__label { cursor: default; pointer-events: none; }

.order-form__label { display: flex; align-items: center; font-size: 14px; padding: 0 10px; color: #6a7989; position: absolute; width: 100%; height: 100%; color: #5198FC; cursor: text; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }

.order-form__label-content { position: relative; display: block; width: 100%; transform-origin: 0% 50%; transition: transform 0.3s; }

.graphic { position: absolute; top: 0; left: 0; fill: none; }

.graphic--madoka { transform: scale3d(1, -1, 1); transition: stroke-dashoffset 0.3s; pointer-events: none; stroke: #5198FC; stroke-width: 4px; stroke-dasharray: 962; stroke-dashoffset: 558; }

.input--filled .input__label--madoka { cursor: default; pointer-events: none; }

.input--filled .graphic--madoka { stroke-dashoffset: 0; }

.input--filled .order-form__label-content { transform: scale3d(0.81, 0.81, 1) translate3d(0, -40px, 0); }

.slide-item__text { font-family: "LSTKDayfly-Prototype"; font-size: calc(20px + (64 - 20) * ((100vw - 320px) / (1920 - 320))); color: #fff; margin-right: 30px; padding: 0; transform: rotate(-360deg) scale(0.15); opacity: 0; color: #000; transition: all 2s; line-height: 1.8; }

.slide-item__img { display: flex; align-items: center; transition: all 2s; opacity: 0; transform: scale(0.15); }

.slide-item__content { display: flex; justify-content: space-around; align-items: center; margin: 0 20px; padding-top: 10%; padding-bottom: 20%; }

.footer { padding: 50px 0; background-color: #c1c1c1; background-image: url(../img/footer-bg.jpg); background-size: cover; background-position: center center; }

.footer__content { padding: 50px 0; text-align: center; color: #fff; }

.footer__title { font-size: 18px; margin-bottom: 15px; }

.copyright { text-align: center; }

.copyright__text { color: #fff; }

.f-social { padding: 38px 0 0 0; }

.f-social__item { display: inline-block; margin-right: 13px; }

.f-social__link { position: relative; display: block; width: 43px; height: 43px; border-radius: 50%; transition: transform 0.3s; }

.f-social__link svg { position: absolute; width: 1em; height: 1em; top: 0; right: 0; bottom: 0; left: 0; margin: auto; font-size: 23px; fill: #fff; }

.f-social__link:hover { transform: rotate(360deg) scale(1.15); }

.f-tw-icon { background: #3b5997; }

.f-fb-icon { background: #3ec2f3; }

.f-g-icon { background: #e44c41; }

.slick-active .rotateIn { transform: rotate(0deg) scale(1); opacity: 1; transition: all 2s; }

.reset-animation .slide-item__text { transform: rotate(-360deg) scale(0); transition: all 0s; }

.slick-active .zoomIn { opacity: 1; transform: scale(1); }
